body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page{
    display: flex;
}
.header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    position: fixed;
    top: 0;
    z-index: 100 ;
    background-color: rgba(33, 33, 33, 1.98);
    width: 100vw;
}
.header_logo{
    margin-left: 25px;
}
.header_input,.header_left,.header_right{
    display: flex;
    align-items: center;
    justify-content: center;
}
.search{
      background-color: hsl(0, 0%, 7%);
      width: 30vw;
      border: 1px solid grey;
      color: white;
      height: 100%;
      margin-top: 15px;
    
}
.header_input{
    
    
    height: 35px;
    
    text-align: center;
    
    
}
.menuIcon{
    color: white;
}

.searchButton{
    width: 45px;
    margin-top: 15px;
    
    
}
.searchIcon{
    color: #606060;
}

.header_icon{
    margin-right: 10px;
    color: white;
}
.header_icon,.header_logo,.searchButton,.menuIcon:hover{
    cursor: pointer;
}
.sidebar{

    position: fixed;
    height: 100vh;
    top: 12%;
    z-index: 100;

    
    background-color: #212121;
    width: 15vw;
    
}
.hr{
    color: rgb(88, 85, 85);
}
.sidebarRow{
    display: flex;
    align-items: center;
}

.sidebarRow_icon{
    color: white;
    font-size: large !important;
    margin-left: 10px;
}
.sidebarRow_title{
    flex: 1 1;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;
    color: white;

}
.sidebarRow:hover{
    cursor: pointer;
    background-color: rgb(97, 97, 97);
}

.sidebarRow:hover>.sidebarRow_icon{
    color: red;
}

.sidebarRow:hover>.sidebarRow_title{
    font-weight: bold;
}

.sidebarRow.selected{
    background-color: rgb(97, 97, 97);
}
.sidebarRow.selected>.sidebarRow_icon{
    color: red;
}
.sidebarRow.selected>.sidebarRow_title{
    font-weight: bold;
}
.videoPage{
    top: 12%;
    position: absolute;
    left: 17%;
    
    background-color: #181818;
    display: flex;
    flex-wrap: wrap;
    width: 85vw;

}
.recom{
    background-color: #181818;
}
.upper{
    display: flex;
}
.lower{
    display: flex;
    width: 100%;
    
    font-size: 12px;
    color: #aeaeae;
    font-weight:400;
    padding: 0;
    margin-top: 1px;
    
      
}
.channelLogo{
    margin-left: 10px;
    border-radius: 100px;
}
.videoCard{
    width: 270px;
    height: 280px;
    margin-top: 200px;
    color:white;
    margin: 20px 20px;
}
.videoCard:hover{
    background-color: black;
    cursor: pointer;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    -webkit-transform: scale(110%);
            transform: scale(110%);
    transition-delay: 1s;
}
.videoTitle{
    margin-left: 20px;
    font-size:medium;
    font-weight: 600;
}
.channelName{
    
    font-size: 12px;
    color: #aeaeae;
    font-weight:400px;
    padding: 0;
    margin-top: 10px;
}
.channelName:hover{
    color: white;
    font-weight: 700;
}
.timeStamp{
    margin-left: 5px;
}

